import React, {useRef, useState} from 'react';
import {SafeAreaView, StyleSheet} from 'react-native';
import * as eva from '@eva-design/eva';
import {
  ApplicationProvider,
  CheckBox,
  IconRegistry,
  Layout,
  RangeDatepicker,
  Text,
} from '@ui-kitten/components';
import TextCM from './src/components/Text';
import {EvaIconsPack} from '@ui-kitten/eva-icons';
import {default as theme} from './custom-theme.json'; // <-- Import app theme
import {default as mapping} from './mapping.json'; // <-- Import app mapping
import {AssetIconsPack} from './src/AssetIconsPack';
import HeaderCM from './src/components/Header';
function App(): React.JSX.Element {
  const ref = useRef(null);
  const [checked, setChecked] = React.useState(false);
  const [range, setRange] = React.useState({});
  return (
    <>
      {/* <IconRegistry icons={[EvaIconsPack, AssetIconsPack]} /> */}
      <ApplicationProvider
        {...eva}
        theme={{...eva.light, ...theme}}
        customMapping={mapping as any}>
        <HeaderCM />
        <SafeAreaView style={{flex: 1}}>
          <Layout
            style={{
              flex: 1,
              justifyContent: 'center',
              alignItems: 'center',
            }}>
            <TextCM style={{color: theme['color-primary-700']}}>
              Welcome to UI Kitten
            </TextCM>
            <Text category="h2">dddd</Text>
            <CheckBox
              ref={ref}
              checked={checked}
              onChange={checked => {
                setChecked(checked);
              }}
              status="primary">
              {() => <TextCM style={{color: 'red'}}>hello</TextCM>}
            </CheckBox>

            <RangeDatepicker
              range={range}
              onSelect={nextRange => setRange(nextRange)}
              style={{backgroundColor: '#fff'}}
              controlStyle={{backgroundColor: '#fff'}}
            />
          </Layout>
        </SafeAreaView>
      </ApplicationProvider>
    </>
  );
}
export default App;
